<template>
  <section class="deal" id="event">
    <div class="content">
      <h3 class="title">
        {{ title }} <b>{{ title_orange }}</b>
      </h3>
      <p>{{ text }}</p>

      <div class="count-down" data-aos="flip-up" data-aos-deplay="200">
        <div
          v-for="(count, index) in contentItems"
          :key="`count-${index}`"
          :class="count.class"
        >
          {{ countEvent() }}

          <h3 :id="count.id">{{ count.id_tx }}</h3>
          <span> {{ count.text }} </span>
        </div>
      </div>
    </div>
  </section>
</template>

<script>


export default {
  data() {
    return {
      title: " Deals Of The ",
      title_orange: "Weeks",
      text: " The sale day has come, what are you waiting for, look for healthy foods in this discount. ",

      contentItems: [
        {
          class: "box",
          id: "day",
          id_tx: "00",
          text: "day",
        },

        {
          class: "box",
          id: "hour",
          id_tx: "00",
          text: "hour",
        },

        {
          class: "box",
          id: "minute",
          id_tx: "00",
          text: "minute",
        },

        {
          class: "box",
          id: "second",
          id_tx: "00",
          text: "second",
        },
      ],

      
    };
  },

  methods: {
    countEvent() {
      let countDate = new Date("june 30, 2022 00:00:00").getTime();

      function countDown() {
        let now = new Date().getTime();
        let gap = countDate - now;

        let second = 1000;
        let minute = second * 60;
        let hour = minute * 60;
        let day = hour * 24;

        let d = Math.floor(gap / day);
        let h = Math.floor((gap % day) / hour);
        let m = Math.floor((gap % hour) / minute);
        let s = Math.floor((gap % minute) / second);

        document.getElementById("day").innerText = d;
        document.getElementById("hour").innerText = h;
        document.getElementById("minute").innerText = m;
        document.getElementById("second").innerText = s;
      }

      setInterval(function () {
        countDown();
      }, 1000);
    },
  },
};
</script>

<style lang="scss">
* {
  font-family: "Nunito", sans-serif;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
  text-transform: capitalize;
  transition: all 0.2s linear;
}

html {
  font-size: 62.6%;

  &::-webkit-scrollbar {
    display: none;
  }
}

.deal {
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url(./image/deal.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  min-height: 80vh;
  position: relative;

  .content {
    position: absolute;
    top: 25%;
    left: 0;
    max-width: 60rem;
    padding: 2rem 7%;

    .title {
      color: #fff;
      font-size: 4rem;

      b {
        color: #ff6924;
      }
    }

    p {
      text-transform: none;
      font-size: 1.8rem;
      padding: 1rem 0;
      color: #fff;
    }

    .count-down {
      display: flex;
      gap: 1rem;
      padding-right: 0.5rem;

      .box {
        border-radius: 0.3rem;
        text-align: center;
        padding: 0.7rem 1.5rem;
        background: #ff6924;
        color: #fff;

        h3 {
          font-size: 3rem;
        }

        span {
          font-size: 1.5rem;
        }
      }
    }
  }
}

@media (max-width: 1200px) {
  html {
    font-size: 55%;
  }
}

@media (max-width: 991px) {
  section {
    padding: 2rem;
  }
}

@media (max-width: 768px) {
}

@media (max-width: 678px) {
  html {
    font-size: 50%;
  }

  .deal {
    .content {
      padding: 1rem 5%;
      text-align: center;

      .count-down {
        justify-content: center;
      }
    }
  }
}

@media (max-width: 450px) {
  html {
    font-size: 50%;
  }
}
</style>
