<template>
  <section class="blog container" id="blog">
    <div class="row">
      <h1 class="heading">discounted <span>products</span></h1>
      <carousel
        :autoplay="true"
        :autoHeight="true"
        :margin="15"
        :responsive="responsive"
      >
        <!-- <template slot="prev"><span class="prev">prev</span></template> -->
        
        <div 
          v-for="(box, index) in boxItem"
          :key="`box-${index}`"
          :class="box.id"
        >
          <img :src="box.image" alt="" />
          <h3>{{ box.name }}</h3>
          <div class="price">
            <span class="price-old"> {{ box.price_new }} </span>
            <span class="price-new"> {{ box.price_old }} </span>
          </div>

          <div
            v-for="(popup, index) in popUp"
            :key="`popup-${index}`"
            :class="popup.id"
          >
            <a class="btn-popup"> {{ popup.text }}</a>
          </div>
        </div>

        <!-- <template slot="next"><span class="next">next</span></template> -->
      </carousel>
    </div>
  </section>
</template>

<script>

import carousel from "vue-owl-carousel";
export default {
  name: "comp-blog",
  
  data: () => {
    return {
      responsive: {
        0: { items: 2, nav: false },
        600: { items: 3, nav: true },
        900: { items: 6, nav: true },
      },

      boxItem: [
        {
          id: "box",
          image: require("../components/image/cr.png"),
          name: "Chanh Tươi",
          price_new: "666.66",
          price_old: "999.99",
        },

        {
          id: "box",
          image: require("../components/image/chanh.png"),
          name: "Chanh Tươi",
          price_new: "666.66",
          price_old: "999.99",
        },

        {
          id: "box",
          image: require("../components/image/db.png"),
          name: "Chanh Tươi",
          price_new: "666.66",
          price_old: "999.99",
        },

        {
          id: "box",
          image: require("../components/image/cr.png"),
          name: "Chanh Tươi",
          price_new: "666.66",
          price_old: "999.99",
        },

        {
          id: "box",
          image: require("../components/image/chanh.png"),
          name: "Chanh Tươi",
          price_new: "666.66",
          price_old: "999.99",
        },

        {
          id: "box",
          image: require("../components/image/cr.png"),
          name: "Chanh Tươi",
          price_new: "666.66",
          price_old: "999.99",
        },

        {
          id: "box",
          image: require("../components/image/db.png"),
          name: "Chanh Tươi",
          price_new: "666.66",
          price_old: "999.99",
        },

        {
          id: "box",
          image: require("../components/image/chanh.png"),
          name: "Chanh Tươi",
          price_new: "666.66",
          price_old: "999.99",
        },
      ],

      popUp: [
        {
          id: "list-model",
          text: "popup",
        },
      ],
    };
  },
  components: { carousel },
  
};
</script>

<style lang="scss">
* {
  font-family: "Nunito", sans-serif;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
  text-transform: capitalize;
  transition: all 0.2s linear;
}

html {
  font-size: 62.6%;

  &::-webkit-scrollbar {
    display: none;
  }
}

.btn-about {
  padding: 0.5rem 1.4rem;
  font-size: 1.5rem;
  font-weight: 600;
  color: #333;
  background: #fff;
  border: 0.2rem solid #ff7800;
  border-radius: 2rem;

  &:hover {
    letter-spacing: 0.1rem;
  }
}

.heading {
  text-align: center;
  font-size: 3rem;
  color: #333;
  padding-bottom: 2rem;
  span {
    color: #3cb815;
  }
}

section {
  padding: 3rem 5%;
}

.blog {
  .row {
    .box {
      position: relative;
      z-index: 1 !important;
      background: #fff;
      border-bottom-right-radius: 5rem;
      border-top-left-radius: 1rem;
      border-top-right-radius: 1rem;
      border: 0.1rem solid rgba(138, 138, 138, 0.2);
      padding: 2rem;
      overflow: hidden;

      img {
        width: 100%;
        max-width: 100%;
      }

      h3 {
        font-size: 1.7rem;
        color: 000039;
      }

      .price {
        display: flex;

        .price-old {
          text-decoration: line-through;
          color: #ff7800;
          font-size: 1.3rem;
          padding-right: 0.5rem;
        }

        .price-new {
          color: #000039;
          font-size: 1.7rem;
        }
      }

      &:hover{
        cursor: pointer;
      }
    }

    .list-model {
      position: absolute;
      bottom: -100%;
      left: 0;
      right: 0;
      border-bottom-right-radius: 5rem;
      border-top-left-radius: 5rem;
      background: #ff7800;
      padding: 1.1rem 0;
      text-align: center;

      .btn-popup {
        color: #fff;
        font-size: 1.7rem;
        text-transform: none;
      }
    }

    .box:hover .list-model {
      bottom: 0;
      cursor: pointer;
      transition: 0.5s ease-in-out;
    }
  }

  .owl-nav{
    display: none;
  }

  .owl-stage{
    padding-bottom: 2rem;
  }

  .owl-theme .owl-dots .owl-dot.active span, .owl-theme .owl-dots .owl-dot:hover span{
    background: #ff7800;
  }
}
</style>
